<template>
    <div>
        버튼이름 : 
        <button @click="onClick">
            {{ name }}
        </button>
        <div>
            Emit 입력 : 
            <input type="text" v-model="value">

            <button @click="onEmit">{{ name }} 전달</button>
        </div>
        <br>
        <br>
    </div>
</template>

<script>
export default {
    name : "CommonButton",
    props: {
        name: String,
    },
    data(){
        return {
            value : "",
        };
    },
    methods: {
        onClick(){
            console.log("공통 버튼 클릭!");
        },
        onEmit(){
            this.$emit("setInput", this.value);
        },
    },
};
</script>